
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
  
body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #333;
    line-height: 1.6;
}
  
.welcome-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}
  
  /* Header styles */
.welcome-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;
    border-bottom: 1px solid #eaeaea;
}
  
.logo {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-color: black;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: bold;
}
  
.main-nav ul {
    display: flex;
    list-style: none;
}
  
.main-nav ul li {
    margin-left: 30px;
}
  
.main-nav ul li a {
    text-decoration: none;
    color: #333;
    font-weight: 500;
    transition: color 0.3s;
}
  
.main-nav ul li a:hover {
    color: #000;
}
  
.auth-buttons {
    display: flex;
    gap: 15px;
}
  
.button {
    display: inline-block;
    padding: 12px 24px;
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    text-decoration: none;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s;
}
  
.button-login {
    background-color: white;
    color: #333;
    border: 1px solid #e5e7eb;
}
  
.button-login:hover {
    background-color: #f9fafb;
}
  
.button-signup {
    background-color: black;
    color: white;
    border: 1px solid black;
}
  
.button-signup:hover {
    background-color: #1a1a1a;
}
  
  /* Hero section */
  .hero-section {
    text-align: center;
    padding: 80px 0;
  }
  
  .hero-section h1 {
    font-size: 48px;
    font-weight: 800;
    margin-bottom: 20px;
    letter-spacing: -0.025em;
  }
  
  .subtitle {
    font-size: 20px;
    color: #666;
    max-width: 700px;
    margin: 0 auto;
  }
  
  /* About section */
  .about-section {
    padding: 80px 0;
    text-align: center;
  }
  
  .about-section h2 {
    font-size: 36px;
    margin-bottom: 20px;
  }
  
  .about-section > p {
    max-width: 800px;
    margin: 0 auto 40px;
    font-size: 18px;
    color: #555;
  }
  
  .features {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 30px;
    margin-top: 40px;
  }
  
  .feature {
    flex: 1;
    min-width: 250px;
    padding: 30px;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  }
  
  .feature-icon {
    font-size: 36px;
    margin-bottom: 15px;
  }
  
  .feature h3 {
    margin-bottom: 15px;
    font-size: 20px;
  }
  
  /* Chapters section */
  .chapters-section {
    padding: 80px 0;
    text-align: center;
    background-color: #f5f5f5;
  }
  
  .chapters-section h2 {
    font-size: 36px;
    margin-bottom: 20px;
  }
  
  .chapters-section > p {
    max-width: 800px;
    margin: 0 auto 40px;
    font-size: 18px;
    color: #555;
  }
  
  .chapter-cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 30px;
    margin-top: 40px;
  }
  
  .chapter-card {
    background-color: white;
    border-radius: 8px;
    padding: 30px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    text-align: left;
    transition: transform 0.3s, box-shadow 0.3s;
  }
  
  .chapter-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
  }
  
  .chapter-card h3 {
    margin-bottom: 15px;
    font-size: 20px;
  }
  
  .chapter-card p {
    color: #666;
    margin-bottom: 20px;
    min-height: 80px;
  }
  
  .chapter-stats {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    font-size: 14px;
    color: #777;
  }
  
  .chapter-button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: black;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .chapter-button:hover {
    background-color: #1a1a1a;
  }
  
  
  
  
  
  
  /* Responsive design */
  @media (max-width: 768px) {
    .home-header {
      flex-direction: column;
      gap: 20px;
    }
    
    .main-nav ul {
      margin-top: 20px;
    }
    
    .main-nav ul li {
      margin-left: 15px;
      margin-right: 15px;
    }
    
    .features {
      flex-direction: column;
    }
    
    
  }